.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'roboto';
}

.img-animation{
  width: 400px;
}
.img-cover{
 text-align: center;
}

.title{
  font-weight: 600;
  text-align: center;
  white-space: nowrap;
  background: -webkit-linear-gradient(#036B9B, #00E197);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.functions {
  display: flex;
  justify-content: center;
}

.button {
  margin-inline: 5px;
  font-size: 24px;
  font-weight: 600;
  color: white;
  background: rgb(202, 88, 6);
  padding: 10px 20px;
  box-shadow: 5px 5px 5px gray;
  cursor: pointer;
  border-radius: 7px;
  white-space: nowrap;
  margin-bottom: 10px;
}

.import {
  background: rgb(88, 172, 5);
}

.blank {
  background: rgb(202, 88, 6);
}

.import:active {
  background: rgb(11, 126, 17);
}

.blank:active {
  background: rgb(122, 56, 8);
}

@media only screen and (max-width: 450px) {
  .functions {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .img-animation{
    width: 300px;
  }
}